<template>
  <div class="h-100">
    {{userName}}
    <md-progress-bar class="md-accent" v-if="xloading" md-mode="indeterminate"></md-progress-bar>

    <table class="i-table table table-striped table-bordered table-sm table-hover">
      <thead>
        <tr>
          <th class="stickyHead">Key</th>
          <th class="stickyHead">Value</th>
          <th class="stickyHead">Save</th>
          <th class="stickyHead">Delete</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="(setting, index) in settings" :key="index">
          <td>
            {{setting.text}}
            <p>
              <small>{{setting.code}}</small>
            </p>
          </td>
          <td>
            <div v-if="setting.validValues === undefined || setting.validValues.length < 1">
              <input type="text" class="form-control" v-model="setting.currentValue" />
            </div>
            <div v-else>
              <select class="form-control" v-model="setting.currentValue">
                <option
                  v-for="(xVal,indx) in setting.validValues"
                  :key="indx"
                  :value="xVal"
                >{{xVal}}</option>
              </select>
            </div>
          </td>

          <td>
            <button class="btn btn-sm btn-link btn-outline-secondary" @click="saveSetting(setting)">
              <i class="fa fa-save"></i>
            </button>
          </td>
          <td>
            <button class="btn btn-sm btn-outline-danger" @click="deleteSetting(setting)">
              <i class="fa fa-trash-alt"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  props: {
    userNameASProp: {
      type: String,
      required: false,
      default: ""
    }
  },

  computed: {
    userName: function() {
      if (this.userNameASProp.trim().length > 0) {
        return this.userNameASProp;
      } else {
        return this.userNameAsRoute;
      }
    }
  },
  data: function() {
    return {
      processing: false,
      userNameAsRoute: this.$route.params.username,
      settings: {}
    };
  },
  mounted() {
    this.loadsettings();
  },

  methods: {
    initialize() {
      this.loadsettings();
    },

    //----------------------------------
    saveSetting(setting) {
      var vm = this;

      this.runWebService(
        "u/settingssave",
        {
          setting: setting.code,
          newValue: setting.currentValue,
          user: vm.userName
        },

        function() {
          vm.processing = true;
        },
        function(respons) {
          //console.log(respons);
          vm.processing = false;

          if (respons.data.status == "s" || respons.data.status == "S") {
            setting.value = respons.data.newvalue;
            vm.$notify({
              type: "success",
              title: "Done",
              message: "User settings updated successfully"
            });
          } else {
            vm.$notify({
              type: "danger",
              title: respons.data.message
            });
          }
        },
        function(error) {
          vm.processing = false;
        }
      );
    },
    //----------------------------------
    deleteSetting(setting) {
      var vm = this;
      setting.currentValue = "";
      this.runWebService(
        "u/settingsdlt",
        {
          setting: setting.code,
          user: vm.userName
        },

        function() {
          vm.processing = true;
        },
        function(respons) {
         // console.log(respons);
          vm.processing = false;

          if (respons.data.status == "s" || respons.data.status == "S") {
            setting.value = respons.data.newvalue;
            vm.$notify({
              type: "success",
              title: "Done",
              message: "User settings updated successfully"
            });
          } else {
            vm.$notify({
              type: "danger",
              title: respons.data.message
            });
          }
        },
        function(error) {
          vm.processing = false;
        }
      );
    },
    //----------------------------------
    loadsettings() {
      var vm = this;
      this.runWebService(
        "u/settings",
        {
          user: vm.userName
        },

        function() {
          vm.processing = true;
        },
        function(respons) {
         // console.log(respons);
          vm.processing = false;
          if (respons.data.status == "S") {
            vm.settings = respons.data.data.settings;
          } else {
            vm.$notify({
              type: "danger",
              title: respons.data.message
            });
          }
        },
        function(error) {
          vm.processing = false;
        }
      );
    }
  }
};
</script>




<style>
.i-table {
  white-space: nowrap;
  overflow: visible;
}
.checkbox-custom {
  display: none;
}

.checkbox-custom-label {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  margin: 5px;
  cursor: pointer;
}

.checkbox-custom + .checkbox-custom-label:before {
  content: "";
  background: #fff;
  border-radius: 5px;
  border: 1px solid #ddd;
  display: inline-block;
  vertical-align: middle;

  width: 24px;
  height: 24px;
  padding: 2px;
  margin-right: 10px;
}

.checkbox-custom:checked + .checkbox-custom-label:after {
  content: "";
  padding: 2px;
  position: absolute;
  width: 12px;
  height: 25px;
  border: solid #53bafc;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -6px;
  left: 10px;
}

.content-container {
  padding: 8px 10px;
  display: flex;
}
</style>



