export const AppMixin = {
  beforeMount() {
    this.turnOffListeners();

  },
  mounted() {
    this.initialize();
    this.setupListeners();


  },
  beforeDestroy() {
    this.turnOffListeners();

  },
  deactivated() {
    this.turnOffListeners();

  },

  //----------------------------------------------------------------------------
  //----------------------------------------------------------------------------

  data: function () {
    return {
      CancelTokenSource: {},
      xloading: false,
      xIsAdmin: false,
      menuVisible: true,
      menuVisibleBackup: true,
      serverChangedCounter: 0

    }
  },

  computed: {
    isAdmin() {
      if (this.xIsAdmin) {
        return this.xIsAdmin
      }
      this.checkIfAdmin()
      return this.xIsAdmin
    },
    getConnectedServerName() {


      return this.$session.get("currentservername")

    },
  },
  //----------------------------------------------------------------------------
  //
  //----------------------------------------------------------------------------


  methods: {
    initialize() {},
    setupListeners() {

    },
    turnOffListeners() {

    },

    beforeChangeRoute(to, from, next, emitEvent, eventValue) {

      console.log(to)
      console.log(from)
      console.log(next)


      if (typeof emitEvent !== 'undefined' &&
        emitEvent.length > 0) {
        eventBus.$emit(emitEvent, eventValue);
      }

      if(to.name=="loginentry" || to.path=="/login")
      {
        next();
        return;
      }

      this.$dialog
        .confirm("Do you really want to leave?")
        .then(function () {
          next();
        })
        .catch(function () {
          next(false);
        });
    },
    //----------------------------------------------------------------------------
    toggleMenu() {
      this.menuVisible = !this.menuVisible;
      this.menuVisibleBackup = this.menuVisible
    },
    hideLeftMenu() {
      this.menuVisibleBackup = this.menuVisible
      this.menuVisible = false
      eventBus.$emit("hideleftsidemenu", true)
    },
    unhideLeftMenu() {

      // this.menuVisible = this.menuVisibleBackup
      // eventBus.$emit("hideleftsidemenu", !this.menuVisibleBackup)
    },
    cancelAxiosRequest() {
      try {
        this.CancelTokenSource.cancel("Operation canceled by the user.");
      } catch (e) {
        console.log(e);
      }
    },

    //----------------------------------------------------------------------------



    //----------------------------------------------------------------------------
    setupAxios() {
      // const sToken =  window.$cookies.get("QSQL_TOKEN");
      const sToken = this.$session.get("QSQL_TOKEN");

      axios.defaults.headers.common = {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: sToken
      };

      return true;
    },

    //----------------------------------------------------------------------------

    openWebSocket(requestId, onMessage, onClose) {
      const sToken = this.$session.get("QSQL_TOKEN");
      //process.backendWSurl in build/webpack.dev/prod
      let ws = new WebSocket(process.backendWSurl + requestId);
      ws.onmessage = msg => {

        let messageData = msg.data
        if (msg.data == "@DONE") {
          messageData = "<strong style='color:red'>-- -- -- -- -- -- -- -- -- -- -- END OF DATA -- -- -- -- -- -- -- -- -- -- --</strong>"
          ws.close();
        }
        console.log("Web" + messageData)
        onMessage(messageData, requestId)
      };
      ws.onclose = () => onClose(requestId);

      return ws;


    },

    getConnectedServerName2() {
      return this.$session.get("currentservername")
    },
    getConnectedServerID() {
      return this.$session.get("currentserver")
    },
    getCurrentUserName() {
      return this.$session.get("currentuser")
    },
    //--------------------------------------------------------------------------
    getBaseUrl() {
      return axios.defaults.baseURL;
    },
    //----------------------------------------------------------------------------

    checkIfAdmin() {
      var vm = this;

      this.runWebService(
        "u/isa", {},
        function () {},
        function (respons) {
          if (respons.data.status == "s" || respons.data.status == "S") {
            vm.xIsAdmin = true;
          } else {
            vm.xIsAdmin = false;
          }
        },
        function (error) {
          vm.xIsAdmin = false;
        }
      );
    },
    //---------------------------------------------------------------
    isEmpty(value) {
      return _.isUndefined(value) || _.isEmpty(value.toString().trim())
    },
    //---------------------------------------------------------------
    isNotEmpty(value) {
      return !this.isEmpty(value)
    },
    //----------------------------------------------------------------------------
    runWebService(url, params, beforeRun, onSucess, onError) {
      //      alert("ok3");
      this.xloading = true
      const CancelToken = axios.CancelToken;
      this.CancelTokenSource = CancelToken.source();
      params["source"] = "web"
      var vm = this;

      if (this.setupAxios()) {
        beforeRun();
        if (axios.defaults.baseURL == undefined) {
          return this.CancelTokenSource;

        }
        axios
          .post(url, params, {
            cancelToken: vm.CancelTokenSource.token
          })
          .then(response => {
            // alert(response);
            if (response.data.token !== undefined) {
              this.$session.set("QSQL_TOKEN", response.data.token);
            }
            try {

              if (response.data.status == 401) {
                notifyReloginRequied(vm,response.data.message)
                throw new Error(response.data.message);
              }
            } catch (e) {

              throw e;
            }
            vm.xloading = false
            onSucess(response);

          })
          .catch(err => {
            vm.xloading = false
            console.log('Errorred '+ err);

            if (axios.isCancel(err)) {
              console.log('Request canceled', err.message);
            } else {

              try {
                if (err.response !== undefined && err.response.status !== undefined && (err.response.status == 401 || err.response.data.status == 401)) {

                  notifyReloginRequied(vm,err.response.data.message)

                }
              } catch (e) {
                onError(err);
                throw e;
              }

            }
            onError(err);
          });
      }
      return this.CancelTokenSource;
    },
    //====================================================================================
    loadRPGEditor: function (monaco) {
      /*----------------------------------------SAMPLE JS START*/
      // Register a new language
      monaco.languages.register({
        id: 'myrpg'
      });
      // Register a tokens provider for the language
      monaco.languages.setMonarchTokensProvider('myrpg', this.getRPGTokenProvicer());
      // Define a new theme that contains only rules that match this language
      monaco.editor.defineTheme('myRPGTheme', {
        base: 'vs-dark',
        inherit: true,
        rules: [{
            token: 'custom-info',
            foreground: '808080'
          },
          {
            token: 'rpgbif',
            foreground: '0025FF',
            fontStyle: 'bold'
          },
          {
            token: 'custom-notice',
            foreground: 'FFA500'
          },
          {
            token: 'custom-date',
            foreground: '008800'
          },
          {
            token: 'endofpgm',
            foreground: '008800',

            fontStyle: 'bold'
          },
        ]
      });
      // Register a completion item provider for the new language
      monaco.languages.registerCompletionItemProvider('myrpg', {
        provideCompletionItems: () => {
          var suggestions = [{
              label: 'simpleText',
              kind: monaco.languages.CompletionItemKind.Text,
              insertText: 'simpleText'
            }, {
              label: 'testing',
              kind: monaco.languages.CompletionItemKind.Keyword,
              insertText: 'testing(${1:condition})',
              insertTextRules: monaco.languages.CompletionItemInsertTextRule
                .InsertAsSnippet
            }, {
              label: 'ifelse',
              kind: monaco.languages.CompletionItemKind.Snippet,
              insertText: [
                'if (${1:condition}) ;',
                '\t$0',
                '} else ;',
                '\t',
                'endif'
              ].join('\n'),
              insertTextRules: monaco.languages.CompletionItemInsertTextRule
                .InsertAsSnippet,
              documentation: 'If-Else Statement'
            }

            , {
              label: 'if',
              kind: monaco.languages.CompletionItemKind.Snippet,
              insertText: [
                'if (${1:condition}) ;',
                '\t',
                'endif'
              ].join('\n'),
              insertTextRules: monaco.languages.CompletionItemInsertTextRule
                .InsertAsSnippet,
              documentation: 'If Statement'
            }

          ];
          return {
            suggestions: suggestions
          };
        }
      });



      /*----------------------------------------SAMPLE CSS END*/
    },


    //====================================================================================
    getRPGTokenProvicer: function () {
      // Create your own language definition here
      // You can safely look at other samples without losing modifications.
      // Modifications are not saved on browser refresh/close though -- copy often!
      return {
        // Set defaultToken to invalid to see what you do not tokenize yet
        // defaultToken: 'invalid',

        keywords: ['closqlcsr', 'EXSR', 'EXEC SQL', 'else', 'endif', 'endsr', 'BEGSR',
          'ACQ', 'ACTGRP', 'ADD', 'ALIAS', 'ALIGN', 'ALLOC', 'ALT', 'ALTSEQ', 'ALWNULL', 'ASCEND', 'AUT', 'BASED', 'BITOFF', 'BITON', 'BLOCK', 'BNDDIR', 'CASE', 'CCSID', 'CHAIN', 'CLASS', 'CLEAR', 'CLOSE', 'COMMIT', 'CONST', 'COPYNEST', 'COPYRIGHT', 'CTDATA', 'CURSYM', 'CVTOPT', 'DATEDIT', 'DATFMT', 'DDS', 'DEBUG', 'DECEDIT', 'DECPREC', 'DELETE', 'DESCEND', 'DEVID', 'DFTACTGRP', 'DFTNAM', 'DIM', 'DO', 'DTAARA', 'ENBPFRCOL', 'END', 'EVAL', 'EVALR', 'EXCEPT', 'EXFMT', 'EXPORT', 'EXPROPTS', 'EXTBININT', 'EXTDESC', 'EXTFILE', 'EXTFLD', 'EXTFMT', 'EXTIND', 'EXTMBR', 'EXTNAME', 'EXTPGM', 'EXTPROC', 'FEOD', 'FIXNBR', 'FLTDIV', 'FOR', 'FORCE', 'FORMLEN', 'FORMOFL', 'FORMSALIGN', 'FROMFILE', 'FTRANS', 'GENLVL', 'IF', 'IGNORE  ', 'IMPORT', 'INCLUDE', 'INDDS', 'INDENT', 'INFDS', 'INFSR', 'INTO', 'INTPREC', 'INZ', 'ITER', 'KEYLOC', 'LANGID', 'LEAVE', 'LEAVESR', 'LEN', 'LIKE', 'LIKEDS', 'LIKEFILE', 'LIKERE  C', 'LOOKUP', 'MAXDEV', 'MOVE', 'MOVEA', 'NEXT', 'NOMAIN', 'NOOPT', 'OCCUR', 'OCCURS', 'OFLIND', 'ON-ERROR', 'OPDESC', 'OPEN', 'OPENOPT', 'OPTIMIZE', 'OPTION', 'OPTIONS', 'OR', 'O  THER', 'OVERLAY', 'PACKEVEN', 'PASS', 'PERRCD', 'PGMNAME', 'PLIST', 'POST', 'PREFIX', 'PRFDTA', 'PROCPTR', 'PRTCTL', 'QUALIFIED', 'RAFDATA', 'READ', 'READC', 'READE', 'READP', 'RE  ADPE', 'RECNO', 'REL', 'RENAME', 'RESET', 'ROLBK', 'RTNPARM', 'SAVEDS', 'SAVEIND', 'SAX', 'SELECT', 'SETGT', 'SETLL', 'SFILE', 'SLN', 'SRTSEQ', 'STATIC', 'STGMDL', 'TAG', 'TEMPLAT  E', 'TESTB', 'TEXT', 'THREAD', 'TIMFMT', 'TOFILE', 'TRUNCNBR', 'UNLOCK', 'UPDATE', 'USROPN', 'USRPRF', 'VALUE', 'VARYING', 'WHEN', 'WRITE', 'XFOOT'
        ],

        starwords: [
          '*Blanks'
        ],

        endofpgm: ['*InLR', 'Return'],

        hspec: [
          'NO-MAIN',
          'ctl-opt'
        ],

        dsepec: [
          'DCL-S',
          'DCL-S',
          'DCL-DS',
          'ZONED',
          'PACKED',
          'CHAR',
          'VARCHAR',

          'dcl-pr',
          'end-pr',
          'dcl-proc',
          'end-proc',
          'dcl-pi',
          'end-pi'
        ],

        bifs: [
          '%ABS',
          '%ADDR',
          '%ALLOC',
          '%BITAND',
          '%BITNOT',
          '%BITOR',
          '%BITXOR',
          '%CHAR',
          '%CHECK',
          '%CHECKR',
          '%DATE',
          '%DAYS',
          '%DEC',
          '%DECH',
          '%DECPOS',
          '%DIFF',
          '%DIV',
          '%EDITC',
          '%EDITFLT',
          '%EDITW',
          '%ELEM',
          '%EOF',
          '%EQUAL',
          '%ERROR',
          '%FIELDS',
          '%FLOAT',
          '%FOUND',
          '%GRAPH',
          '%HANDLER',
          '%HOURS',
          '%INT',
          '%INTH',
          '%KDS',
          '%LEN',
          '%LOOKUPxx',
          '%MINUTES',
          '%MONTHS',
          '%MSECONDS',
          '%NULLIND',
          '%OCCUR',
          '%OPEN',
          '%PADDR',
          '%PARMS',
          '%PARMNUM',
          '%REALLOC',
          '%REM',
          '%REPLACE',
          '%SCAN',
          '%SCANRPL',
          '%SECONDS',
          '%SHTDN',
          '%SIZE',
          '%SQRT',
          '%STATUS',
          '%STR',
          '%SUBARR',
          '%SUBDT',
          '%subst',
          '%THIS',
          '%TIME',
          '%TIMESTAMP',
          '%TLOOKUPxx',
          '%TRIM',
          '%TRIML',
          '%TRIMR',
          '%UCS2',
          '%UNS',
          '%UNSH',
          '%XFOOT',
          '%XLATE',
          '%XML',
          '%YEARS'

        ],



        typeKeywords: [
          'zoned', 'packed', 'char', 'varchar'
        ],

        operators: [
          '=', '>', '<', 'not', '~', '?', ':', '==', '<=', '>=', '!=',
          'and', 'or', '+', '-', '*', '**', '/', '^', '%',
          '<<', '>>', '>>>', '+=', '-=', '*=', '/=', '&=', '|=', '^=',
          '%=', '<<=', '>>=', '>>>='
        ],

        // we include these common regular expressions
        symbols: /[=><!~?:&|+\-*\/\^%]+/,

        // C# style strings
        escapes: /\\(?:[abfnrtv\\"']|x[0-9A-Fa-f]{1,4}|u[0-9A-Fa-f]{4}|U[0-9A-Fa-f]{8})/,

        ignoreCase: true,
        // The main tokenizer for our languages
        tokenizer: {
          root: [
            [/\w*\-\w*/, {
              cases: {
                '@dsepec': 'keyword',

                '@default': 'identifier'
              }
            }],

            // identifiers and keywords
            [/[\*a-zA-Z_\-$][\w\-$]*/, {
              cases: {
                '@typeKeywords': 'keyword',
                '@keywords': 'keyword',
                '@bifs': 'rpgbif',
                '@endofpgm': 'endofpgm',
                '@default': 'identifier'
              }
            }],


            [/[A-Z][\w\$]*/, 'type.identifier'], // to show class names nicely
            [/\%[\w\-$]*/, {
              cases: {

                '@bifs': 'rpgbif',

                '@default': 'identifier'
              }
            }],


            // whitespace
            {
              include: '@whitespace'
            },

            // delimiters and operators
            [/[{}()\[\]]/, '@brackets'],
            [/[<>](?!@symbols)/, '@brackets'],
            [/@symbols/, {
              cases: {
                '@operators': 'operator',
                '@default': ''
              }
            }],

            // @ annotations.
            // As an example, we emit a debugging log message on these tokens.
            // Note: message are supressed during the first load -- change some lines to see them.
            [/@\s*[a-zA-Z_\$][\w\$]*/, {
              token: 'annotation',
              log: 'annotation token: $0'
            }],

            // numbers
            [/\d*\.\d+([eE][\-+]?\d+)?/, 'number.float'],
            [/0[xX][0-9a-fA-F]+/, 'number.hex'],
            [/\d+/, 'number'],

            // delimiter: after number because of .\d floats
            [/[;,.]/, 'delimiter'],

            // strings
            [/'([^'\\]|\\.)*$/, 'string.invalid'], // non-teminated string
            [/'/, {
              token: 'string.quote',
              bracket: '@open',
              next: '@string'
            }],

            // characters
            [/'[^\\']'/, 'string'],
            [/(')(@escapes)(')/, ['string', 'string.escape', 'string']],
            [/'/, 'string.invalid']
          ],

          comment: [
            [/[^\/*]+/, 'comment'],
            [/\/\*/, 'comment', '@push'], // nested comment
            ["\\*/", 'comment', '@pop'],
            [/[\/*]/, 'comment'],


          ],

          string: [
            [/[^\\']+/, 'string'],
            [/@escapes/, 'string.escape'],
            [/\\./, 'string.escape.invalid'],
            [/'/, {
              token: 'string.quote',
              bracket: '@close',
              next: '@pop'
            }]
          ],
          ///qsys.lib/sgoyal.lib/qrpglesrc.file/test_1.mbr
          whitespace: [
            [/^.{6}\*.*/, 'comment'],
            [/[ \t\r\n]+/, 'white'],
            [/\/\*/, 'comment', '@comment'],
            [/\/\/.*$/, 'comment'],

          ],
        },
      };

    }



    //======================================================================================
  }
};



